@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap);
body,html,.container{width:100vw;height:100vh;font-family:'Roboto', sans-serif}html{overflow:hidden}body{margin:0}ul{margin:0;padding:0}.container{display:flex}

.note{display:flex;justify-content:space-between;padding:10px 20px;position:relative;color:#FFFFFF;min-height:20px;cursor:pointer}.note--selected{background-color:#FDD760;color:#150B03}.note__title{white-space:nowrap;overflow:hidden;text-overflow:ellipsis}.note__block{position:absolute;right:40px}.note__actions{border:none;background-color:#EECD63;padding:0;position:absolute;right:10px;width:20px;height:20px;border-radius:4px;margin-left:20px}.note__actions:focus{outline:none}.note__actions::after{display:flex;position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);color:#FFFFFF;content:'\2807'}

.site-bar{background-color:#252525;position:relative;height:100%}.site-bar__list{overflow-y:scroll;overflow-x:hidden;max-width:100%;max-height:90%;min-height:90%;scrollbar-color:#FDD760 #252525;scrollbar-width:thin;width:230px}.site-bar__list::-webkit-scrollbar{width:5px;background-color:#252525}.site-bar__list::-webkit-scrollbar-thumb{background-color:#FDD760;border-radius:9em}.site-bar__btn-add{display:flex;align-items:center;justify-content:center;width:100%;padding:0;background-color:transparent;border:none;font-size:20px;color:#FDD760;cursor:pointer}

.search{position:absolute;bottom:30px;display:flex;justify-content:center;width:100%}.search__input{text-align:center;border:none;border-radius:4px;background:#343233;color:#FFFFFF;max-width:90%;font-size:16px;padding:5px}.search__input:focus{outline:none}

.popup{position:absolute;right:50px;width:100px;z-index:999;border:1px solid #D9D8D8;border-radius:5px;box-shadow:-1px 10px 42px 23px rgba(34,60,80,0.29)}.popup button{width:100%;padding:5px 0;color:#3F3F44;background:#F0EDE8;border:none;border-bottom:1px solid #D9D8D8;cursor:pointer}.popup button:focus{outline:none}.popup button:hover{background:#2F86FD;color:#FFFFFF}

.content{display:flex;flex-direction:column;width:100%;border:none;outline:none}.content__title{flex-grow:1;font-weight:bold;font-size:20px;border:none;padding:10px 30px}.content__title:focus{outline:none}.content__field{border:none;flex-grow:40;line-height:2em;font-size:16px;padding:20px 30px}.content__field:focus{outline:none}.content__info{text-align:center}

